const faultRouter = {
  // 运营中心->维修管理
  path: '/fault',
  component: () => import('@/views/OperationsCenter/fault/fault'),
  meta: {
    title: '维修管理'
  },
  children: [{
    path: '',
    redirect: 'faultList',
    component: () => import('@/views/OperationsCenter/fault/faultList/faultList'),
    meta: {
      title: '故障设备列表'
    }
  },{
    path: '/fault/faultList',
    component: () => import('@/views/OperationsCenter/fault/faultList/faultList'),
    meta: {
      title: '故障设备列表'
    }
  },{
    path: '/fault/repairOrder',
    component: () => import('@/views/OperationsCenter/fault/repairOrder/repairOrder'),
    meta: {
      title: '维修工单列表'
    }
  }]
}

export default faultRouter