const terminalRouter = {
  // 运营中心->设备管理
  path: '/terminal',
  component: () => import('@/views/OperationsCenter/terminal/terminal'),
  meta: {
    title: '设备管理',
    requiresAuth: true
  },
  children: [{
    path: '',
    redirect: 'list',
    component: () => import('@/views/OperationsCenter/terminal/list/list'),
    meta: {
      title: '设备列表'
    }
  },{
    path: '/terminal/list',
    component: () => import('@/views/OperationsCenter/terminal/list/list'),
    meta: {
      title: '设备列表'
    }
  },{
    path: '/terminal/group',
    component: () => import('@/views/OperationsCenter/terminal/group/group'),
    meta: {
      title: '设备状态'
    }
  },{
    path: '/terminal/configList',
    component: () => import('@/views/OperationsCenter/terminal/configList/configList'),
    meta: {
      title: '运营配置'
    }
  },{
    path: '*',
    component: () => import('@/views/NotFoundPage/NotFoundPage')
  }]
}

export default terminalRouter