import terminalRouter from '@/router/OperationsCenter/terminal'
import productRouter from '@/router/OperationsCenter/product'
import locationRouter from '@/router/OperationsCenter/location'
import orderRouter from '@/router/OperationsCenter/order'
import warehouseRouter from '@/router/OperationsCenter/warehouse'
import replenishRouter from '@/router/OperationsCenter/replenish'
import faultRouter from '@/router/OperationsCenter/fault'
import adsIncomeRouter from '@/router/OperationsCenter/adsIncome'
import rechargeRouter from '@/router/OperationsCenter/recharge'

const baseRouter = [{
  path: '/',
  component: () => import('@/views/HomePage/HomePage'),
  meta: {
    title: '工作台'
  },
  children: [{
    path: '',
    redirect: 'dashboard',
    component: () => import('@/views/dashboard/dashboard')
  },{
    path: '/dashboard',
    component: () => import('@/views/dashboard/dashboard'),
    meta: {
      title: '概况'
    }
  }].concat(
    terminalRouter, // 运营中心->设备管理
    productRouter, // 运营中心->商品管理
    locationRouter, // 运营中心->点位管理
    orderRouter, // 运营中心->订单管理
    warehouseRouter, // 运营中心->仓库管理
    replenishRouter, // 运营中心->补货管理
    faultRouter, // 运营中心->维修管理
    adsIncomeRouter, // 运营中心->广告管理
    rechargeRouter // 运营中心->充值卡管理
  )
},{
  path: '/admin',
  name: 'admin',
  component: () => import('@/views/AdminPage/AdminPage')
},{
  path: '/404',
  name: 'noFund',
  component: () => import('@/views/NotFoundPage/NotFoundPage')
},{
  path: '*',
  component: () => import('@/views/NotFoundPage/NotFoundPage')
}]

export default baseRouter