import Vue from 'vue'
import App from './App.vue'
import iView from 'iview'
import 'iview/dist/styles/iview.css'
import store from './store'
import baseMixin from '@/mixins/base'

import router from './router'

Vue.config.productionTip = false

Vue.mixin(baseMixin)

Vue.use(iView)

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
