import request from '@/utils/request'

export default {
  methods: {
    $img (name) {
      return require(`@/assets/images/${name}`)
    },
    $api ( method, url, params = {} ) {
      return new Promise((resolve,reject) => {
        request({
          method: method,
          url: url,
          data: params
        }).then(res => {
          resolve(res.data)
        }).catch(( error ) => {
          reject(error)
        })
      })
    }
  }
}