const menu = {
  state: {
    MenuActive: [],
    MenuItem: '',
    Menu: []
  },
  mutations: {
    pushMenuActive: (state, val) => {
      state.MenuActive[0] = val
    },
    pushMenuItem: (state, val) => {
      state.MenuItem = val
    },
    setMenu: (state, menu) => {
      state.Menu = menu
    }
  }
}

export default menu